.titleDataTable{
    margin-top: 60px;
    margin-bottom: 100px;
    flex: auto;
    text-align: center;
}
.EditButtons{
    margin:30px;
    text-align: center;
}

.titleDataTable h3{
    font-size: 60px;
    font-weight: 600;

}

#title {
    text-align: center;
    font-family: arial, sans-serif;
  }
  
  #languages {
    text-align: center;
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    border: 3px solid #ddd;
    width: 100%;
  }
  
  #languages td, #languages th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #languages tr:nth-child(even){background-color: #f2f2f2;}
  
  #languages tr:hover {background-color: #ddd;}
  
  #languages th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #4CAF50;
    color: white;
  }
  .dataTable img{
      height: 90px;
      width: 120px;
  }
  .dataTable{
      text-align: center;
      margin-bottom: 100px;
  }
  .dataTable select{
    height: 40px;
  }

  .btnAddDataTable{
      margin-right: 10px;
      font-size: 20px;
    align-content: flex-end
  }
  .BTNCLASS{
    font-size: 20px;

  }